'use strict';

var define = require('define-properties');

var getPolyfill = require('./polyfill');

module.exports = function shimArrayPrototypeFind() {
  var polyfill = getPolyfill();
  define(Array.prototype, {
    find: polyfill
  }, {
    find: function find() {
      return Array.prototype.find !== polyfill;
    }
  });
  return polyfill;
};